@media (min-width: 769px) {
  .bread-crumbs-list {
    height: 90px;
  }
  .brief-box-half {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .brief-box-half .ui-group {
    flex-basis: calc(100% / 2 - 13px);
  }
  .select-list-choose {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .select-list-choose__item {
    flex-basis: calc(100% / 2 - 15px);
  }
  .go-home, .phone {
    padding-top: 17px;
  }
  .choose-list {
    display: flex;
    flex-wrap: wrap;
  }
}

@media (min-width: 993px) {
  header {
    padding: 60px 0 46px;
  }
}

@media (min-width: 1201px) {
  .brief-box-half .ui-group {
    flex-basis: calc(100% / 2 - 35px);
  }
  .choose-list {
    justify-content: center;
  }
  .choose-list li {
    max-width: 300px;
    flex-basis: 300px;
  }
  .choose-list li:not(:first-child):not(:last-child) {
    margin-left: 90px;
    margin-right: 90px;
  }
  .select-list-choose__item {
    padding-right: 87px;
  }
}

@media (min-width: 993px) and (max-width: 1200px) {
  .choose-list li {
    max-width: calc(100% / 3 - 20px);
    flex-basis: calc(100% / 3 - 20px);
  }
}

@media (min-width: 769px) and (max-width: 992px) {
  .choose-list li {
    max-width: calc(100% / 2 - 15px);
    flex-basis: calc(100% / 2 - 15px);
  }
  .choose-list li:last-child {
    margin: 80px auto 0;
  }
}

@media (max-width: 992px) {
  header {
    padding: 30px 0 25px;
  }
}

@media (max-width: 768px) {
  .ui-label-field {
    line-height: 1.4;
  }
  .bread-crumbs-current::after {
    display: none;
  }
  .bread-crumbs-current::before {
    background-color: #33ddfb;
    width: 22px;
    height: 1px;
    margin-right: 12px;
    display: inline-block;
    vertical-align: middle;
    content: '';
  }
  header, .phone {
    text-align: center;
  }
  .go-home {
    padding-bottom: 20px;
  }
  .main-title {
    margin-bottom: 15px;
  }
  .choose-list li:not(:last-child) {
    margin-bottom: 80px;
  }
  .bread-crumbs {
    margin-bottom: 50px;
  }
  .bread-crumbs-list {
    padding-bottom: 35px;
  }
  .bread-crumbs-item::after {
    display: none;
  }
  .bread-crumbs-back-link {
    width: 135px;
    margin: 0 auto;
  }
  .bread-crumbs-back-link .arrow {
    display: none;
  }
}

@media (max-width: 414px) {
  .checkbox-custom-label:not(:last-of-type) {
    margin-bottom: 15px;
  }
  .ui-field {
    padding-top: 6px;
  }
  .phone {
    font-size: 21px;
  }
}

@media (min-width: 769px) and (max-width: 1200px) {
  .choose-list {
    justify-content: space-between;
  }
}
